@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap";
* {
  box-sizing: border-box;
}

body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  text-align: center;
  color: #fff;
  background-color: #1e90ff;
  padding: 10px;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  width: 300px;
  border: none;
  border-radius: 5px 0 0 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form .search-button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 10px;
}

.search-form .search-button img {
  width: 20px;
  height: 20px;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.gallery-title {
  text-align: center;
  visibility: hidden;
  margin-top: 0;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  background-color: #f0f0f0;
  border-radius: 5px;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.photo-card .info {
  margin-top: 10px;
}

.photo-card .info-item {
  margin-bottom: 5px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #1e90ff;
  border: none;
  margin: 0 auto;
  padding: 10px;
  display: block;
}

.load-more:hover {
  opacity: .8;
}

